.headers{
    text-align: center;
    background: black;
    float: none;
    height: 80px;
}
.two {
background: url("fon.png");
    padding: 50px 20px;
    text-align: center;
    border-radius: 1px;
border: 1px solid cornflowerblue;
}
.two h1 {
    font-family: 'Open Sans', sans-serif;
    position: relative;
    color: #5C2610;
    font-size: 3em;
    font-weight: normal;
    line-height: 1;
    padding: 10px 0;
    margin: 0;
    display: inline-block;
}
.two h1:before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 80%;
    height: 200%;
    border-radius: 50%/30%;
    border: 6px solid #5C2610;
    border-left-color: transparent;
    border-right-color: transparent;
}
@media (max-width: 420px) {
    .two h1 {font-size: 2em;}
}
